.homework-committed-content {
  margin: 30px;
  background: #fff;
  min-height: calc(100vh - 120px);
}
.homework-committed-content .ant-input-search {
  margin: 10px 0;
}
.homework-committed-content .have-read-studentList {
  background: #fff;
}
.homework-committed-content .have-read-studentList .have-read-top {
  padding: 0 30px;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eee;
  margin-bottom: 20px;
}
.homework-committed-content .have-read-studentList .have-read-top .ant-select-selection {
  width: 187.81px;
  height: 28px;
  background: #F4F4F4;
  border-radius: 2px;
}
.homework-committed-content .have-read-studentList .have-read-top .ant-select-selection__rendered {
  line-height: 28px;
}
.homework-committed-content .have-read-studentList .have-read-top .ant-input-search input {
  float: right;
  width: 260px;
  height: 27px;
  background: #F3F3F3;
  border-radius: 14px;
  border: none;
}
.homework-committed-content .have-read-studentList .have-read-list {
  padding: 0 30px;
}
.homework-committed-content .have-read-studentList .have-read-list .ant-table-row {
  height: 46px !important;
}
.homework-committed-content .have-read-studentList .have-read-list .ant-table-row:nth-child(odd) {
  background: #F7F9FB;
}
.homework-committed-content .have-read-studentList .have-read-list tr:hover td {
  background: none !important;
}
.homework-committed-content .have-read-studentList .have-read-list th {
  background: #fff !important;
}
.homework-committed-content .have-read-studentList .have-read-list th,
.homework-committed-content .have-read-studentList .have-read-list td {
  text-align: center !important;
  padding: 0 16px;
  border: none;
}
.homework-committed-content .have-read-studentList .have-read-list .ant-table-placeholder {
  border: none;
}
.text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
  cursor: pointer;
}
